<template>
	<div class='cashflow'>
		<!-- 当日收支 -->
		<el-row :gutter="20">
			<!-- 当前收入 -->
			<el-col :span="8">
				<el-card shadow='always'>
					<div class='contant'>
						<div class='topbox'>本日収入</div>
						<div class='income'>+ {{today.income}}円</div>
					</div>
				</el-card>
			</el-col>
			<!-- 本日支出 -->
			<el-col :span="8">
				<el-card shadow='always'>
					<div class="contant">
						<div class='topbox'>本日支出</div>
						<div class='expenses'>- {{today.expense}}円</div>
					</div>
				</el-card>
			</el-col>
			<!-- 本日余剰 -->
			<el-col :span="8">
				<el-card shadow='always'>
					<div class="contant">
						<div class="topbox">本日余剰</div>
						<div class='rest'>{{today.income-today.expense}}円</div>
					</div>
				</el-card>
			</el-col>
		</el-row>
		<!-- 期間選択  追加数据-->
		<el-card style="margin-top: 10px">
			<el-row :gutter="20">
				<el-col :span="20" style="display: flex;flex-direction: row; align-items: center;">
					<span style="padding-right: 0.5rem;">期間を選択してください</span>
					<el-date-picker v-model="datevalue" type="daterange" align="center" unlink-panels range-separator="To"
						:picker-options="pickerOptions" @change="dataChangeHandler">
					</el-date-picker>
				</el-col>
				<el-col :span="4">
					<!-- 追加数据按钮 -->
					<el-tooltip content='add' placement="top" effect="light" :enterable="false">
						<el-button type='danger' icon='el-icon-plus' circle @click="addDataDialogFormVisible = true"></el-button>
					</el-tooltip>
					
				</el-col>
			</el-row>
		</el-card>
		<!-- 表格数据 -->
		<el-card class='table' :body-style="{padding:'12px'}">
			<el-table :data="tableData" style="width: 100%" :row-class-name="tableRowClassName">
				<el-table-column type="expand">
					<template slot-scope="props">
						<el-form label-position="left" inline class="demo-table-expand">
							<el-form-item label="備考">
								<span>{{ props.row.remark }}</span>
							</el-form-item>
							<el-form-item label="ファイル" v-if="props.row.hasfile==1">
								<a :href="downloadurl" @click="downloadfile(props.row.id)"><el-button type="info" icon='el-icon-download' round></el-button></el-button></a>							
							</el-form-item>
						</el-form>
					</template>
				</el-table-column>
				<el-table-column type='index' label="#">
				</el-table-column>
				<el-table-column prop="date" label="日期" width="120">
				</el-table-column>
				<el-table-column prop="client" label="取引先" width="180">
				</el-table-column>
				<el-table-column prop="usage" label="用途" width="500">
				</el-table-column>
				<el-table-column prop="amount" label="金額">
				</el-table-column>
				<el-table-column>
					<template slot-scope="props">
						<el-tooltip content='edit' placement="top" effect="light" :enterable="false">
							<el-button type='primary' icon='el-icon-edit' size='mini' @click="editTableData(props.row)">
							</el-button>
						</el-tooltip>
						<el-tooltip content='delete' placement="top" effect="light" :enterable="false">
							<el-button type='danger' icon='el-icon-delete' size='mini' @click='deleteTableData(props.row.id)'>
							</el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
			  @size-change="handleSizeChange"
			  @current-change="handleCurrentChange"
			  :current-page="pageinfo.currentpage"
			  :page-sizes="[15, 50, 100]"
			  :page-size="pageinfo.pagesize"
			  layout="total, sizes, prev, pager, next, jumper"
			  :total="pageinfo.total">
			</el-pagination>
		</el-card>	
		
		<!-- 添加数据的对话框 -->
		<el-dialog title="記録添加" :visible.sync="addDataDialogFormVisible" width="40%" @close="closeDialog()">
			<el-form :model="addData" label-width="120px" ref="addDataFormRef" :rules="rules">
				<el-form-item label="タイプ" prop="type">
					<el-col :span="20">
						<el-select style="width: 100%;" v-model="addData.type" placeholder="">
							<el-option label="支出" value="0"></el-option>
							<el-option label="収入" value="1"></el-option>
						</el-select>
					</el-col>
				</el-form-item>
				<el-form-item label="金額" prop="amount">
					<el-col :span="20">
						<el-input v-model="addData.amount"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="日付" prop="date">
					<el-col :span="20">
						<el-date-picker v-model='addData.date' type="date" style="width: 100%;" value-format="yyyy-MM-dd"></el-date-picker>
					</el-col>
				</el-form-item>
				<el-form-item label="用途" prop="usage">
					<el-col :span="20">
						<el-input type="textarea" v-model='addData.usage' maxlength="50"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="取引先" prop="client">
					<el-col :span="20">
						<el-input v-model="addData.client"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="備考" prop="remark">
					<el-col :span="20">
						<el-input type="textarea" v-model='addData.remark' maxlength="100"></el-input>
					</el-col>
				</el-form-item>
				
				<el-form-item label="ファイル" prop="file">
					<el-col :span="20">
						<!-- :on-preview="handlePreview" -->
						<el-upload
						  class="upload-demo"
						  ref="upload"
						  :action="actionURL"
						  drag
						  name='file'
						  :limit="1"
						  :on-remove="handleRemove"
						  :file-list="fileList"
						  :auto-upload="false">
						  <i class="el-icon-upload"></i>
						  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
						</el-upload>
					</el-col>
				</el-form-item>
			</el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="closeDialog()">戻　る</el-button>
		    <el-button type="primary" @click="validateForm()">追　加</el-button>
		  </div>
		</el-dialog>
		<!-- 修改数据的对话框 -->
		<el-dialog title="記録編集" :visible.sync="editDataDialogFormVisible" width="40%" @close="closeEditDialog()">
			<el-form :model="editData" label-width="120px" ref="editDataFormRef" :rules="rules">
				<el-form-item label="タイプ" prop="type">
					<el-col :span="20">
						<el-select style="width: 100%;" v-model="editData.type==0?'支出':'收入'" placeholder="" >
							<el-option label="支出" value="0"></el-option>
							<el-option label="収入" value="1"></el-option>
						</el-select>
					</el-col>
				</el-form-item>
				<el-form-item label="金額" prop="amount">
					<el-col :span="20">
						<el-input v-model="editData.amount"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="日付" prop="date">
					<el-col :span="20">
						<el-date-picker v-model='editData.date' type="date" style="width: 100%;" value-format="yyyy-MM-dd" ></el-date-picker>
					</el-col>
				</el-form-item>
				<el-form-item label="用途" prop="usage">
					<el-col :span="20">
						<el-input type="textarea" v-model='editData.usage' maxlength="50"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="取引先" prop="client">
					<el-col :span="20">
						<el-input v-model="editData.client"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="備考" prop="remark">
					<el-col :span="20">
						<el-input type="textarea" v-model='editData.remark' maxlength="100"></el-input>
					</el-col>
				</el-form-item>
				
				<el-form-item label="ファイル" prop="file">
					<el-col :span="20">
						<!-- :on-preview="handlePreview" -->
						<el-upload
						  class="upload-demo"
						  ref="update"
						  :action="actionURL"
						  drag
						  name='file'
						  :limit="1"
						  :on-remove="handleRemove"
						  :file-list="fileList"
						  :auto-upload="false">
						  <i class="el-icon-upload"></i>
						  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
						</el-upload>
					</el-col>
				</el-form-item>
			</el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="closeEditDialog()">戻　る</el-button>
		    <el-button type="primary" @click="validateEditForm()">編　集</el-button>
		  </div>
		</el-dialog>
	</div>
</template>
<script>
	export default {
		data() {
			var checkamount=(rule, value, cb)=>{
				const regamount = /^[0-9]/
				if(regamount.test(value)){
					return cb()
				}
				cb(new Error("Please input right amount"))
			}
			return {
				datevalue: '',
				addDataDialogFormVisible: false,
				editDataDialogFormVisible:false,
				formLabelWidth:'120px',
				fileList: [],
				downloadurl: "",
				addData:{
					date:'',
					amount:'',
					type:'',
					usage:'', 
					remark:'',
					client:''
				},
				editData:{},
				pageinfo:{
					pagesize:15,
					total:0,
					currentpage:1
				},
				today:{},
				tableData: [],
				actionURL : "",
				// datepicker
				pickerOptions: {
					shortcuts: [{
						text: '本月',
						onClick(picker) {
							picker.$emit('pick', [new Date(), new Date()]);
						}
					}, {
						text: '一年間',
						onClick(picker) {
							const end = new Date();
							const start = new Date(new Date().getFullYear(), 0);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近六ヶ月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setMonth(start.getMonth() - 6);
							picker.$emit('pick', [start, end]);
						}
					}]
				},
				rules:{
					amount:[{required:true, trigger:'blur'},
							{validator:checkamount, trigger:'blur'}],
					usage:[
							{required:true, trigger:'blur'},
							{max:50, message:"max length is 50",trigger:'blur'}
					],
					remark:[
							{required:true, trigger:'blur'},
							{max:100, message:"max length is 100", trigger:'blur'}
					],
					client:[
							{required:true, trigger:'blur'},
							{max:50, message:"max length is 50",trigger:'blur'}
					],
					date:[
						 { required: true, trigger: 'blur' }
					],
					type:[
						 { required: true, trigger: 'blur' },
					]
				}
			}
		},
		created() {
			this.getData()
		},
		methods:{
			// 当日期选择器发生变化时，向服务器请求数据
			dataChangeHandler(value) {
				this.getData()
			},
			// 向服务器请求数据
			async getData() {
				if(this.datevalue[0]!==undefined&& this.datevalue[1]!==undefined){
					console.log(this.datevalue)
					const {data:res} = await this.$http.post('cashflow/tabledata',{start:this.datevalue[0],end:this.datevalue[1], page:this.pageinfo})
					this.tableData = res.tableData
					this.pageinfo.total = res.total
					this.today = res.today
				}else{
					const {data:res} = await this.$http.post('cashflow/tabledata',{start:'', end:"", page: this.pageinfo})
					this.tableData = res.tableData
					this.pageinfo.total = res.total
					this.today = res.today
				}
			},
			// 用来处理每条数据的背景，根据数据的type来区分
			tableRowClassName({row,rowIndex}) {
				if (row.type==0) {
					return 'warning-row';
				} else {
					return 'success-row';
				}
			},
			// 与服务器交互，删除数据
			async deleteTableData(id){
				const {data:res} = await this.$http.post('cashflow/delete',{'id':id})
				if(res.code == 200){
					this.$message.success("delete data successful")
					this.getData()
				}else{
					this.$message.error("delete data failed")
				}
			},
			// 与服务器交互，编辑数据
			async editTableData(row){
				this.editData = row
				this.editDataDialogFormVisible = true	
			},
			// 关闭添加项目对话框
			closeDialog(){
				this.$refs['addDataFormRef'].resetFields()
				this.addDataDialogFormVisible = false
			},
			// 关闭编辑项目对话框
			closeEditDialog(){
				this.$refs['editDataFormRef'].resetFields()
				this.editDataDialogFormVisible = false
			},
			// 验证添加数据表单
			validateForm(){
				this.$refs['addDataFormRef'].validate((valid)=>{
					if(valid){
						this.addTableData()
					}else{
						return false
					}
				})
			},
			// 验证编辑数据表单
			validateEditForm(){
				this.$refs['editDataFormRef'].validate((valid)=>{
					if(valid){
						this.submitEditData()
					}else{
						return false
					}
				})
			},
			// 提交更新数据
			async submitEditData(){
				const {data:res} = await this.$http.post('cashflow/update', this.editData)
				this.actionURL =  "http://127.0.0.1:8000/api/cashflow/uploadfile/"+this.editData.id
				this.$refs['update'].submit()
				if(res.code == 200){
					this.$message.success("upload data successful")
					this.closeEditDialog()
					this.getData()
				}else{
					this.$message.error("upload data failed")
				}
			},
			// 提交数据
			async addTableData(){
				// 首先向服务器提交表单数据，
				console.log(this.addData)
				const {data:res} = await this.$http.post('cashflow/additem',this.addData)
				this.actionURL =  "http://127.0.0.1:8000/api/cashflow/uploadfile/"+res.id
				// console.log(res)
				if(res.code == 200){
					await this.$message.info("Uploaded data")
					//如果提交成功在判断是否存在文件
					// console.log("提交数据URL:" + this.actionURL)
					this.$refs['upload'].submit()
				}else{
					this.$message.error("Data upload failed")
				}
				
				// this.$refs.addDataFormRef.resetFields()
				this.closeDialog()
				this.getData()
			},
			handleRemove(file,fileList){
				var index = fileList.indexOf(file)
				fileList.splice(index,1)
			},
			handleSizeChange(pagesize){
				// console.log("change pagesize to "+pagesize)
				this.pageinfo.currentpage = 1
				this.pageinfo.pagesize = pagesize
				this.getData()
			},
			handleCurrentChange(currentpage){
				// console.log("change currentpage to "+currentpage)
				this.pageinfo.currentpage = currentpage
				this.getData()
			},
			async downloadfile(id){
				// await this.$http.get('cashflow/downloadfile/'+id)
				this.downloadurl = 'http://127.0.0.1:8000/api/cashflow/downloadfile/'+id
			}
		}
	}
</script>
<style>
	.topbox{
		margin: 0;
		padding: 0;
	}
	.income {
		font-weight: bolder;
		color: #000000;
		font-size: 30px;
		margin-top: 0px;
		width: 100%;
	}

	.expenses {
		font-weight: bolder;
		color: #c3737c;
		font-size: 30px;
		margin-top: 0px;
		width: 100%;
	}

	.rest {
		font-weight: bolder;
		color: #7dc373;
		font-size: 30px;
		margin-top: 0px;
		width: 100%;
	}

	.contant {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin: 0;
		padding: 0;
	}

	.table {
		margin-top: 10px;
	}

	.el-table .warning-row {
		background: oldlace;
	}

	.el-table .success-row {
		background: #c8f5a3;
	}
	.demo-table-expand {
		font-size: large;
	}
	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
		}
	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 10px;
		width: 100%;
		
	}	
</style>
